<template>
    <div class="paper-theme aside-page">
        <sidebar class="wide">
            <div slot="brand">
                <div class="logo">
                    <img src="/assets/lambda/images/light.svg" alt="Puzzle Visual Builder">
                </div>
                <div class="language-switcher">
                    <Select  v-if="has_language && languages.length >= 2" v-model="selectedLang" @on-change="switchLanguage" >
                        <Option  v-for="lang in languages" :value="lang.code" :key="lang.index">
                            {{ lang.label }}
                        </Option>
                    </Select>
                </div>
            </div>
            <ul>
                <!--               -->
                <li class="sub-title">
                    <span>{{ lang.db }}</span>
                </li>
<!--                <li>-->
<!--                    <router-link to="/dbdesign">-->
<!--                        <i class="ti-server"></i>-->
<!--                        <span>Table</span>-->
<!--                    </router-link>-->
<!--                </li>-->
                <li>
                    <router-link to="/datasource">
                        <i class="ti-server"></i>
                        <span>View</span>
                    </router-link>
                </li>
                <li class="divider"></li>
                <li class="sub-title">
                    <span>{{ lang.usersAndUserGroups }}</span>
                </li>
                <li>
                    <router-link to="/module/agent">
                        <i class="ti-user"></i>
                        <span>{{ lang.users }}</span>
                    </router-link>
                </li>
                <li>
                    <router-link to="/role">
                        <i class="ti-unlock"></i>
                        <span>{{ lang.userGroupsPermission }}</span>
                    </router-link>
                </li>
                <li class="divider"></li>
                <li>
                    <a href="/lambda/upload" target="_blank">
                        <i class="ti-cloud-up"></i>
                        <span>{{ lang.sendDBSchema }}</span>
                    </a>
                </li>
                <li>
                    <a href="/lambda/async" target="_blank">
                        <i class="ti-cloud-down"></i>
                        <span>{{ lang.systemUpdate }}</span>
                    </a>
                </li>
            </ul>
            <div slot="aside-bottom">
                <ul>
                    <li>
                        <a @click="logoutModal = true">
                            <Icon type="ios-log-out"></Icon>
                            <span>{{ common._logout }}</span>
                        </a>
                    </li>
                </ul>
            </div>
        </sidebar>
        <main>
            <router-view></router-view>
        </main>

        <Modal v-model="logoutModal" :closable="false" width="252" class="logout-modal">
            <p slot="header" style="display:none;"></p>
            <div style="text-align:center">
                <a @click="logout()">
                    <Icon type="md-log-out"/>
                    {{ common._logout }}
                </a>
                <a @click="cancel()">
                    <Icon type="md-refresh"/>
                    {{ common._cancel }}
                </a>
            </div>
            <div slot="footer" style="display:none;">
                <form action="/auth/logout"></form>
            </div>
        </Modal>
    </div>
</template>

<script>
import {mapGetters} from "vuex";
import {loadLanguageAsync} from "@lambda-platform/locale";

export default {
    computed: {
        ...mapGetters({
            user: "user"
        }),
        lang() {
            const labels = ['db', 'usersAndUserGroups', 'users', 'userGroupsPermission', 'sendDBSchema', 'systemUpdate'];
            return labels.reduce((obj, key, i) => {
                obj[key] = this.$t('adminModule.' + labels[i]);
                return obj;
            }, {});
        },
        common() {
            const labels = ['_logout', '_cancel'];
            return labels.reduce((obj, key, i) => {
                obj[key] = this.$t('user.' + labels[i]);

                return obj;
            }, {});
        }
    },
    data() {
        const app_logo = window.init.app_logo;
        const app_text = window.init.app_text;
        return {
            logoutModal: false,
            app_logo: app_logo,
            app_text: app_text,
            languages: window.lambda.languages,
            has_language: window.lambda.has_language,
            selectedLang: localStorage.getItem("lang") == null ? window.lambda.default_language : localStorage.getItem("lang"),
        };
    },
    components: {},
    beforeMount() {
        if (this.selectedLang != "mn_MN") {
            loadLanguageAsync(this.selectedLang);
        }
    },
    methods: {
        switchLanguage(val) {
            this.selectedLang = val;
            loadLanguageAsync(val);
        },
        logout() {
            axios.post("/auth/logout", {}).then(o => {
                window.location = "/auth/login";
            });
        },
        // uploadSchema() {
        //     axios.post("/auth/logout", {}).then(o => {
        //         window.location = "/auth/login";
        //     });
        // },
        cancel() {
            this.$data.logoutModal = false;
        }
    }
};
</script>
